body {
    margin: 0;
    padding: 0;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: #031321;

    a {
        position: relative;
        display: inline-block;
        padding: 15px 30px;
        color: #2196F3;
        text-transform: uppercase;
        letter-spacing: 4px;
        text-decoration: none;
        font-size: 24px;
        overflow: hidden;
        transition: 0.2s;

        &:hover {
            // color: #255784;
            // background: #2196f3;
            // box-shadow: 0 0 10px #2196f3, 0 0 40px #2196f3, 0 0 80px #2196f3;
            // transition-delay: 1s;

            span:nth-child(1) {
                left: 100%;
                transition: 1s;
            }
        }

        span {
            position: absolute;
            display: block;

            &:nth-child(1) {
                top: 0;
                left: -100%;
                width: 100%;
                height: 2px;
                background: liner-gradient(90deg, transparent, #2196f3);
            }
        }
    }
}